import React from "react";
//import { SearchContext } from "../../App";
import { useDispatch } from "react-redux";

import searchLogo from "../../assets/img/search-line.svg";
import style from "./Search.module.scss";
//import debounce from "lodash.debounce";
import { setIsSearch } from "../../redux/filterSlice/sliceFilter";
import debounce from "lodash.debounce";

// const debounceTimeOut = debounce(() => {
//   //    ,
//   //        
//   console.log("vbhcasd");
// }, 1000);

const Search: React.FC = () => {
  const [value, setValue] = React.useState(""); //     
  //const { setSearchValue } = React.useContext(SearchContext); // ,        

  //const isSearch = useSelector((state) => state.itemsSlice);
  const dispatch = useDispatch();

  const inputRef = React.useRef<HTMLInputElement>(null);
  const onClickClear = () => {
    dispatch(setIsSearch(""));
    setValue(""); //    ,  
    //document.querySelector("input").focus();

    //   ,    ,  
    //         
    //  js ,  ,      DOM-
    //   useRef
    inputRef.current?.focus();
    //   ,    ,  
    //console.log(inputRef.current); //current    ,    , .. .focus
  };

  //   ,        1,  
  //    
  //-   ,   ,  ,  

  const updateSearch = React.useCallback(
    // ,   ,   
    debounce((str: string) => {
      dispatch(setIsSearch(str)); //   ,  
    }, 1000),
    []
  );

  // event -  (ChangeEvent) - HTML- (HTMLInputElement)
  // Handler -  ,   ,
  //  - ChangeEventHandler
  //  - MouseEventHandler
  const onChangeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    setValue(event.target.value); //    
    updateSearch(event.target.value); //      ,     1 
  };

  return (
    <div className={style.search}>
      <input
        ref={inputRef}
        value={value}
        onChange={onChangeInput}
        placeholder="..."
      />
      <img src={searchLogo} alt="Search Logo" />

      {value && (
        <svg
          onClick={onClickClear}
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 24 24"
          fill="grey"
        >
          <path d="M11.9997 10.5865L16.9495 5.63672L18.3637 7.05093L13.4139 12.0007L18.3637 16.9504L16.9495 18.3646L11.9997 13.4149L7.04996 18.3646L5.63574 16.9504L10.5855 12.0007L5.63574 7.05093L7.04996 5.63672L11.9997 10.5865Z"></path>
        </svg>
      )}
    </div>
  );
};

export default Search;
