import React from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";

import { addCartValue } from "../redux/cartSlice/slicesCart";
import { CartItem } from "../redux/cartSlice/typeCarts";

type PelmeniBlockProps = {
  id: string;
  name: string;
  price: number;
  count: number;
  sizes: number[];
  imageUrl: string;
  types: number[];
};

const PelmeniBlock: React.FC<PelmeniBlockProps> = ({
  id,
  name,
  price,
  imageUrl,
  sizes,
  types,
}) => {
  const typesNames = [" ", " "];

  const dispatch = useDispatch();

  const [activeType, setActiveType] = useState(0);
  const [activeSize, setActiveSize] = useState(0);

  const [count, setCount] = useState(0);

  const updateBtn = () => {
    setCount(count + 1);
  };

  const onClickAdd = (e: React.MouseEvent<HTMLButtonElement>) => {
    updateBtn();
    const item: CartItem = {
      id,
      name,
      price,
      imageUrl,
      sizes: sizes[activeSize],
      types: typesNames[activeType],
      count: 0,
    };
    dispatch(addCartValue(item));
    notLink(e);
  };

  const notLink = (e: any) => {
    e.preventDefault();
  };

  return (
    <div className="pizza-block">
      <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
      <h4 className="pizza-block__title">{name}</h4>
      <div className="pizza-block__selector">
        <ul onClick={(e) => notLink(e)}>
          {types.map((type) => {
            return (
              <li
                key={type}
                className={activeType === type ? "active" : ""}
                onClick={() => setActiveType(type)}
              >
                {typesNames[type]}
              </li>
            );
          })}
        </ul>
        <ul onClick={(e) => notLink(e)}>
          {sizes.map((size, i) => {
            return (
              <li
                key={size}
                onClick={() => setActiveSize(i)}
                className={activeSize === i ? "active" : ""}
              >
                {size} .
              </li>
            );
          })}
        </ul>
      </div>
      <div className="pizza-block__bottom">
        <div className="pizza-block__price"> {price} </div>
        <button
          onClick={(e) => onClickAdd(e)}
          className="button button--outline button--add"
        >
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
              fill="white"
            />
          </svg>
          <span></span>
          <i>{count}</i>
        </button>
      </div>
    </div>
  );
};

export default PelmeniBlock;
