import { configureStore } from "@reduxjs/toolkit";
import filterSlice from "./filterSlice/sliceFilter";
import itemsSlice from "./itemsSlice/sliceItems";
import cartSlice from "./cartSlice/slicesCart";
import { useDispatch } from "react-redux";

export const store = configureStore({
  //store - Redux-,    ,   Redux Toolkit
  reducer: {
    //,  
    filterSlice, //counter - ,  -  ,    ,  -    filterSlice
    //     -  - counter -  ,    
    // counter   ,    filterSlice
    //    ,  filterSlice: filterSlice == filterSlice   
    // -      ,  filterSlice
    itemsSlice,
    cartSlice,
  },
});

//   RTK
// RootState -       
//           
export type RootState = ReturnType<typeof store.getState>;

//   dispatch,  
//       
type AppDispatch = typeof store.dispatch;
export const useAppDispatch = () => useDispatch<AppDispatch>();
